import React, { Component } from 'react';
import Flickity from 'flickity';
import { api } from './workApi';

export default class Work extends Component {
	constructor(props) {
		super(props);
		this.state = {
			selectedIndex: 0,
			workApi: api,
			moveX: null,
		}
	}
	componentDidMount() {
    const carousel = document.getElementById('slider');
    const options = {
      cellSelector: '.project',
      contain: true,
      initialIndex: 0,
      accessibility: true,
      prevNextButtons: true,
      wrapAround: true,
      pageDots: false,
      selectedAttraction: 0.15,
			friction: 0.8
    }
		this.ct = setTimeout(() => { 
			this.props.hideLoader();
			this.flkty = new Flickity(carousel, options);
	    this.flkty.on('cellSelect', () => {
	    	for(let ref in this.refs) { this.refs[ref].classList.remove('slide-reset') }
	    	this.setState({ selectedIndex: this.flkty.selectedIndex }, () => {
	    		this.ft = setTimeout(() => {
		    		this.refs[this.state.selectedIndex].classList.add('slide-reset');
		    	}, 1500);
	    	});
	    });
	    this.st = setTimeout(() => {
	  		this.refs[this.state.selectedIndex].classList.add('slide-reset');
	  	}, 1500);
	  	if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
				window.addEventListener('deviceorientation', this.updateMoveX, true);
			} 
		}, 500);
  }

  updateMoveX = (e) => this.setState({ moveX: ((e.gamma + 90) * 100) / 180 });
  
  visit = (e) => window.open(e.target.dataset.url, '_blank');

  componentWillUnmount() {
    if (this.flkty) {
      this.flkty.off('cellSelect', this.updateSelected);
      this.flkty.destroy();
    }
    clearTimeout(this.ct);
    clearTimeout(this.ft);
    clearTimeout(this.st);
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
			window.removeEventListener('deviceorientation', this.updateMoveX, true);
		} 
    
  }

	render = () => {
		return (
			<section className="work" id='slider'>
				{
					this.state.workApi.map((obj, i) => {
						return (
							<div
								ref={i}
								key={i} 
								className="project"
								style={{
									backgroundPositionX: this.state.selectedIndex === i ? `${this.state.moveX}%` : '50%'
								}}>
								<div 
									id='snow' 
									style={{
										display: this.state.selectedIndex === 7 ? "block" : "none"
									}}></div>
								<div>
									<h2>
										{
											obj.name.split('').map((letter, i) => {
												return (letter === ' ') ? <div key={i}>&nbsp;&nbsp;&nbsp;</div> : <div style={{transitionDelay: (0.3 + i/20) + 's'}} key={i}>{letter}</div>
											})
										}
									</h2>
									<div className="info">
										<p>{obj.p1}</p>
										<p>{obj.p2}</p>
									</div>
									<button 
										onClick={this.visit} 
										data-url={obj.url}>
										<h3 data-url={obj.url}>Visit</h3>
									</button>
								</div>
							</div>
						);
					})
				}

			</section>
		);
	}
}


