import React, { Component } from 'react';
import Header from './components/header/Header';
import Home from './components/home/Home';
import Menu from './components/menu/Menu';
import Loader from './components/loader/Loader';
import Work from './components/work/Work';

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      height: window.innerHeight,
      burgerClasses: "hamburglar is-open",
      burgerToggle: true,
      menuClasses: "menu",
      loaderClasses: "loader loader-show",
      homeClasses: "home",
      page: "Home",
      pastInitLoad: false
    }
  }

  componentDidMount(){
    window.addEventListener('resize', this.updateHeight);
    setTimeout(() => {
      this.setState({loaderClasses: "loader"});
      setTimeout(() => {
        this.setHomeClasses();
        setTimeout(() => { 
          this.setState({ pastInitLoad: true });
        }, 1000);
      }, 500);
    }, 500);
  }

  updateHeight = () => this.setState({height: window.innerHeight});

  loaded = () => this.setHomeClasses();

  hideLoader = () => this.setState({ loaderClasses: "loader" });

  toggleBurger = () => {
    this.setState((prevState, prevProps) => {
      return {
        burgerToggle : !prevState.burgerToggle,
        burgerClasses : (prevState.burgerClasses === "hamburglar is-closed") ? 
                          "hamburglar is-open" : 
                          "hamburglar is-closed",
        menuClasses: (prevState.menuClasses === "menu") ?
                      "menu menu-show" :
                      "menu"
      }
    });
  }

  setHomeClasses = (e) => {
    setTimeout(() => {  this.setState({homeClasses: 'home home-show'}); }, 1000);
    setTimeout(() => { this.setState({homeClasses: 'home home-show home-reset-delays'}); }, 1500);
  }

  removeHomeClasses = (e) => {
    setTimeout(() => { this.setState({homeClasses: 'home'}) }, 1500);
  }

  navigate = (e) => {
    const p = e.target.dataset.page;
    if(p !== this.state.page) {
      if(!this.state.burgerToggle) {
        this.toggleBurger();
        setTimeout(() => { this.setState({loaderClasses: "loader loader-show"}) }, 700);
        setTimeout(() => { this.setState({page: p}) }, 1200);
      } else {
        this.setState({loaderClasses: "loader loader-show"});
        setTimeout(() => { this.setState({page: p}) }, 500);
      }
    } else {
      if(!this.state.burgerToggle) this.toggleBurger();
    }
  }

  render = () => {
    return (
      <div className="App" style={{height: this.state.height}}>
        <Loader 
          classes={this.state.loaderClasses}
          pastInitLoad={this.state.pastInitLoad} />
        <Header 
          burgerStuff={this.state.burgerClasses}
          burger={this.toggleBurger}
          nav={this.navigate} />
        {
          this.state.page === "Home" ? 
          <Home
            classes={this.state.homeClasses}
            setHomeClasses={this.setHomeClasses}
            removeHomeClasses={this.removeHomeClasses}
            nav={this.navigate}
            loaded={this.loaded}
            hideLoader={this.hideLoader}
            pastInitLoad={this.state.pastInitLoad} />
          : 
          <Work
            hideLoader={this.hideLoader} />
        }
        <Menu 
          classes={this.state.menuClasses}
          nav={this.navigate} />
      </div>
    );
  }
}



