import React, { Component } from 'react';
import $ from 'jquery';
import '../water/water.js';

export default class Home extends Component {
	constructor(props) {
		super(props);
		this.state = {
			moveX: null,
			transDur: '0s',
			rotX: null,
			rotY: null,
			scale: 1,
			bTransDur: '0s',
			boxShadow: null,
			multX: null,
			multY: null
		}
	}

	componentDidMount() {
		if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
			window.addEventListener('deviceorientation', this.updateMoveX, true);
		} 
		if(this.props.pastInitLoad) {
			setTimeout(() => {
				this.props.hideLoader();
				this.props.loaded();
			}, 500);
		}
		$('.home').ripples({
			resolution: 512,
			dropRadius: 20,
			perturbance: 0.04,
		});
	}

	updateMoveX = (e) => this.setState({ moveX: ((e.gamma + 90) * 100) / 180 });

	componentWillUnmount() {
		this.props.removeHomeClasses();
		if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) && window.DeviceOrientationEvent){
			window.addEventListener('deviceorientation', this.updateMoveX, true);
		} 
	}

	handlePageMove = (e) => {
		if(!window.DeviceOrientationEvent) {
			const x = e.type === 'mousemove' ? e.clientX : e.touches[0].clientX;
			const w = window.innerWidth;
			this.setState({ moveX: (x * 100) / w, transDur: '0s'});
		}
	}
	
	handlePageEnter = (e) => {
		if(!window.DeviceOrientationEvent) {
			const x = e.type === 'mouseenter' ? e.clientX : e.touches[0].clientX;
			const w = window.innerWidth;
			this.setState({ moveX: (x * 100) / w, transDur: '0.5s'});
		}
	}

	mouseEnterButton = (e) => {
		const gWY = e.type === 'mouseenter' ? e.clientY : e.touches[0].clientY;
		const gWX = e.type === 'mouseenter' ? e.clientX : e.touches[0].clientX;
		const target = e.target.tagName === "BUTTON" ? e.target : e.target;
		const y = ((target.getBoundingClientRect().left + (target.offsetWidth / 2)) - gWX) / -10;
		let x = ((target.getBoundingClientRect().top + (target.offsetHeight / 2)) - gWY) / 1.75;
		x = x > 0 ? x+=4 : x;
		this.setState({
			rotX: x, rotY: y, bTransDur: '0.5s', scale: 1.1,
			boxShadow: `0 14px 28px rgba(0,0,0,0.5), ${y* -1.85}px ${x}px 10px rgba(0,0,0,0.44)`
		});
	}

	mouseMoveButton = (e) => {
		const gWY = e.type === 'mousemove' ? e.clientY : e.touches[0].clientY;
		const gWX = e.type === 'mousemove' ? e.clientX : e.touches[0].clientX;
		const target = e.target.tagName === "BUTTON" ? e.target : e.target;
		const y = ((target.getBoundingClientRect().left + (target.offsetWidth / 2)) - gWX) / -10;
		let x = ((target.getBoundingClientRect().top + (target.offsetHeight / 2)) - gWY) / 1.75;
		x = x > 0 ? x+=4 : x;
		this.setState({
			rotX: x, rotY: y, bTransDur: '0s', scale: 1.1,
			boxShadow: `0 14px 28px rgba(0,0,0,0.5), ${y* -1.85}px ${x}px 10px rgba(0,0,0,0.44)`
		});
	}

	mouseLeaveButton = (e) => {
		this.setState({rotX: 0, rotY: 0, bTransDur: '0.5s', scale: 1, boxShadow: 'none' });
	}

	render = () => {
		let inc = 7, inc2 = 0, inc3 = 0;
		return (
			<section 
				onMouseMove={this.handlePageMove}
				onMouseEnter={this.handlePageEnter}
				// onTouchMove={this.handlePageMove}
				// onTouchStart={this.handlePageEnter}
				className={this.props.classes}
				style={{
					backgroundPosition: `${this.state.moveX}% 100%`,
					transition: `background-position ${this.state.transDur} 0s, transform 0.5s 0s`
				}}>
				<h2>
					{
						'Alex'.split('').map((letter, i) => {
							inc3 += i;
							return (
								<div 
									key={i}
									style={{
										transition: `transform 0.5s ${2 + (inc3/100)}s, 
																 opacity 0.5s ${2 + (inc3/100)}s, 
																 color 0.3s 0s, text-shadow 0.3s 0s`
									}}>{letter}</div>
							);
						})
					}
				</h2>
				<h2>
					{
						'Figliolia'.split('').map((letter, i) => {
							inc3 += i;
							return (
								<div 
									key={i}
									style={{
										transition: `transform 0.5s ${2 + (inc3/100)}s, 
																 opacity 0.5s ${2 + (inc3/100)}s, 
																 color 0.3s 0s, text-shadow 0.3s 0s`
									}}>{letter}</div>
							);
						})
					}
				</h2>
				<div>
					<div>
						<h2>
							{
								'Front End'.split('').map((letter, i) => {
									if(letter === ' ') {
										return <div 
											key={i}
											className="space"
											style={{
												transitionDelay: inc/140 + 's'
											}}>&nbsp;&nbsp;</div>
									} else {
										inc-=i;
										return <div 
											key={i}
											style={{
												transitionDelay: inc/140 + 's'
											}}>{letter}</div>
									}
								})
							}
						</h2>
						<h2>
							{
								'Developer'.split('').map((letter, i) => {
									inc2 += i;
									return <div 
										key={i}
										style={{
											transitionDelay: inc2/140 + 's'
										}}>{letter}</div>
								})
							}
						</h2>
					</div>
					<button 
						onClick={this.props.nav}
						onMouseEnter={this.mouseEnterButton}
						onMouseMove={this.mouseMoveButton}
						onMouseLeave={this.mouseLeaveButton}
						onTouchStart={this.mouseEnterButton}
						onTouchEnd={this.mouseLeaveButton}
						onTouchMove={this.mouseMoveButton}
						ref="homeButton"
						style={{
							transform: `rotateX(${this.state.rotX}deg) rotateY(${this.state.rotY}deg) skew(-5deg) scale(${this.state.scale})`,
							transitionDuration: this.state.bTransDur,
							boxShadow: this.state.boxShadow
						}}
						data-page="Work">
						<h3 
							onClick={this.props.nav} 
							data-page="Work">Work</h3>
					</button>
				</div>
			</section>
		);
	}
}



